
import { defineComponent, reactive } from 'vue';
import { useDialog } from '../../src';

export default defineComponent({
    setup() {
        const { resolve } = useDialog();

        const form = reactive({
            field1: '',
            field2: 'default',
        });

        function submit() {
            resolve(form);
        }

        return {
            form,
            submit,
        };
    },
});
